import ContactList from "../../components/Contacts/ContactList";
import { useLocation } from "react-router-dom";
import { utilService } from "../../services/util.service";
import { ConstTranslate } from "../../constants/translate";
import { useSelector } from "react-redux";
import { useState, useEffect } from "react";

import { ReactComponent as SortIcon } from "../../assets/icon/sort_arrows.svg"
import { ReactComponent as ArrowDownIcon } from "../../assets/icon/arrow_down.svg"
import { ReactComponent as ExitIcon } from "../../assets/icon/exit.svg"
import { ReactComponent as LineIcon } from "../../assets/icon/line.svg"


const ContactsPage = () => {
    const location = useLocation();
    const [selectedFilters, setSelectedFilters] = useState({})
    // const [sort, setSort] = useState('newest');
    const cat = location.pathname.split("/")[2];

    const handleFilterDelete = (type) => {
        const filters = { ...selectedFilters }
        for (const filter in filters) {
            if (filters[filter] === type) {
                delete filters[filter]
            }
        }
        setSelectedFilters(filters)
    }

    return (
        <div className="body-wrapper space-pb--120 contact-category">
            <div className="breadcrumb-area space-pb--25">
                <MySelect
                    selectedFilters={selectedFilters}
                    setSelectedFilters={setSelectedFilters}
                    cat={cat} />

                {Object.values(selectedFilters).length > 0 && <div className="container flex justify-space mt-4 mb-4">
                    <div className="flex align-center gap-2">
                        <ExitIcon />
                        <h4>Filters {`(${Object.values(selectedFilters).length})`}</h4>
                    </div>

                    <div className="flex align-center gap-2 contact-category-filters">
                        {Object.values(selectedFilters).map(filter => (
                            <h4 key={filter} className="filter__item">
                                {filter}
                                <span onClick={() => handleFilterDelete(filter)}><ExitIcon /></span>
                            </h4>
                        ))}
                    </div>
                </div>}

                <ContactList cat={cat} filters={selectedFilters} title={utilService.getFirstLetterUppercase(cat)} />
            </div>
        </div>
    )
}

const MySelect = ({ setSelectedFilters, selectedFilters, cat }) => {
    const { companies, jobTitles } = useSelector(state => state.categoriesManager)
    const [isFilterOpen, setIsFilterOpen] = useState(null)

    useEffect(() => {
        if (!isFilterOpen) {
            utilService.ignoreBodyScroll()
        }
    }, [isFilterOpen])

    const COMPANY = 'company'
    const TITLE = 'title'

    const onOpenFilter = (type) => {
        setIsFilterOpen({ type: type, accessor: type === COMPANY ? COMPANY : TITLE })
        utilService.ignoreBodyScroll(true)
    }

    const handleSelect = (type, value) => {
        if (type === TITLE) {
            return setSelectedFilters((prevState) => ({ ...prevState, jobTitle: value }))
        }
        setSelectedFilters((prevState) => ({ ...prevState, [type]: value }))
    }

    const getSelectedList = () => {
        if (isFilterOpen) {
            if (isFilterOpen?.type === COMPANY) return companies.filter(company => company.category === cat)
            else return jobTitles
        } else return []
    }

    const isRadioChecked = (value) => {
        return !!Object.values(selectedFilters).find(filter => filter === value)
    }

    return (
        <div className="container contact-filter-by">
            <div className="contact-filter-by__item">
                <SortIcon />
                <h4>Sort By</h4>
            </div>

            <div className={`contact-filter-by__item${isFilterOpen?.type === COMPANY ? " active" : ""}`} onClick={() => onOpenFilter(COMPANY)}>
                <h2>{ConstTranslate.COMPANY}</h2>
                <ArrowDownIcon />
            </div>
            <div className={`contact-filter-by__item${isFilterOpen?.type === TITLE ? " active" : ""}`} onClick={() => onOpenFilter(TITLE)}>
                <h2>{ConstTranslate.TITLE}</h2>
                <ArrowDownIcon />
            </div>

            <div className={`filter-menu__shadow${isFilterOpen ? " active" : ""}`}>
                <div className="filter-menu-container">
                    <div className="filter-menu__list">
                        {
                            getSelectedList().map(entity => {
                                return (
                                    <div key={entity._id} className="filter-menu__item">
                                        <label htmlFor="filter">{entity[isFilterOpen?.accessor]}</label>
                                        <input type="radio" id="filter" checked={isRadioChecked(entity[isFilterOpen?.accessor])} onChange={() => handleSelect(isFilterOpen?.type, entity[isFilterOpen?.accessor])} />
                                    </div>
                                )
                            })
                        }
                    </div>
                    <div className="filter-menu__line" onClick={() => setIsFilterOpen(null)}>
                        <LineIcon />
                    </div>
                </div>
            </div>
        </div>
    )
}

export default ContactsPage